@import "reset.less";
.clearFix:after{
	content:"";display:table;clear:both;
}
.clearFix{
	*zoom:1;
}
h2{font-size: 30px; font-weight: normal;}
.pzTopPn{
	position: absolute;
	top:0;
	left: 0;
	width:100%;
	height:95px;
	background-color:rgba(46,52,61,0.85);
	filter:progid:DXImageTransform.Microsoft.gradient(enabled='true',startColorstr='#DD2e343d',endColorstr='#DD2e343d');
	.pzTopInner{
		width:1000px;
		margin: 0 auto;
		.logo{
			float: left;
			a{
				display: block;
				width:238px;
				height: 61px;
				margin-top: 15px;
				background: url(../images/logo.png);
			}
		}
		.search{float: right;}
		.userInfo{
			float: right;
			margin-top: 30px;
			color: #fefefe;
			font-size: 12px;
			line-height: 26px;
			img{
				border-radius: 50%;
				vertical-align: text-top;
				float: left;
				margin-right: 5px;
			}
		}
	}
	
}
:root .pzTopPn{
	filter:none \9;
}
.pzNavPn{
	position: absolute;
	width:100%;
	height:50px;
	top:95px;
	left: 0;
	z-index: 999;
	background-color:rgba(26,30,36,0.9);
	filter:progid:DXImageTransform.Microsoft.gradient(enabled='true',startColorstr='#DD1a1e24',endColorstr='#DD1a1e24');
	.pzNavInner{
		width:1000px;
		margin:0 auto;
		li{
			float: left;
			width:20%;
			height: 50px;
			a{
				display: block;
				color: #fff;
				font-size: 16px;
				line-height: 50px;
				text-align: center;
				text-decoration: none;
				&:hover{
					text-decoration:none;
				};
			}
		}
		.select{
			a{
				color: #3aaae3;
			}
		}
		.hover{
			background: rgba(0,0,0,.3);
			dd{
				height: 137px;
				padding-left: 60px;
				border-bottom: 1px solid #4b4d52;
				background: rgba(34,36,40,.9);
				color: #a3a3a4;
				position: relative;
				i{
					display: inline-block;
					position: absolute;
					top:40px;
					left: 26px;
					width:22px;
					height: 22px;
					background: #fff;
				}
				.topFont{
					color: #fff;
					padding-top: 40px;
					margin-bottom: 5px;
				}
			}

		}
	}
}
.pzBannerPn{
	height: 695px;
	background: url(../images/topBanner1.jpg) top center no-repeat;
	margin-bottom: 60px;
}
.pzMobilePn{
	width:1000px;
	margin: 0 auto 60px;
	.hd{
		text-align: center;
		margin-bottom: 65px;
	}
	.bd{
		li{
			float: left;
			width:25%;
			.inner{
				text-align: center;
				color: #999;
				img{
					margin-bottom: 40px;
				}
				.tit{
					font-size: 18px;
					color: #333;
					margin-bottom: 20px;
				}
			}
		}
	}
}
.pzNewSuccPn{
	background-color: #f3f3f3;
}

